import {
    SETPLAYING,
    CURRENTINDEX,
    SETCURRENTTRACK,
    SETTRACKLIST,
    RESET,
    NEXTTRACK,
    PREVTRACK,
     PLAYTRACK
} from '../playertypes';


const initialState = {
	currentTrack: null,
	isPlaying: false,
	currentIndex: 0,
	trackList: [],
	repeatStatus: "OFF",
};



const player =(state=initialState , action) =>{
    switch(action.type){
        case RESET:
            return{
                currentTrack: null,
                isPlaying: false,
                currentIndex: 0,
                trackList: [],
                repeatStatus: "OFF",
            }
        case  SETPLAYING:
            return{
                ...state , isPlaying:action.payload
            }
        case CURRENTINDEX:
            return{
                ...state , currentIndex:action.payload
            }
        case PLAYTRACK:
            return{
                currentTrack:action.payload,
                isPlaying:true
            }
        case  SETTRACKLIST:
            return{
                trackList:action.payload.list,
                currentIndex:action.payload.index ? action.payload.index : 0
            }    
        case SETCURRENTTRACK:
            return{
                ...state , currentTrack:action.payload
            }     
        case NEXTTRACK:
            if (state.currentIndex >= state.trackList.length - 1) {
                return{
                    ...state ,currentIndex:0,
                    currentTrack:state.trackList[0]
                }
			} else {
				return{
                    ...state , currentTrack:state.trackList[state.currentIndex + 1],
                    currentIndex:state.currentIndex+1
                }
			}
        case PREVTRACK:
            if (state.currentIndex === 0) {
				return{
                    ...state , currentIndex:state.trackList.length - 1,
                    currentTrack:state.trackList[state.trackList.length - 1]
                }
			} else {
                return{ ...state , 
            currentTrack:state.trackList[state.currentIndex - 1],
                currentIndex:state.currentIndex-1
                }
			}            
            default:
                return state;    
    }
}
export default player;